<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon
        name="arrow-left"
        color="white"
        size="0.48rem"
        class="goback"
        @click="$router.back()"
      />
      <!-- 搜索组件 -->
      <van-search
        v-model="kw"
        v-fofo
        placeholder="请输入搜索关键词"
        background="#007BFF"
        shape="round"
        @input="inputFn"
        @search="searchFn"
      />
    </div>
    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="kw.length !== 0">
      <div
        class="sugg-item"
        v-for="(str, index) in suggesList"
        :key="index"
        v-html="lightFn(str, kw)"
        @click="suggestClickFn(str)"
      ></div>
    </div>
    <!-- 搜索历史 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearFn" />
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list">
        <span
          class="history-item"
          v-for="(str, index) in history"
          :key="index"
          @click="historyClickFn(str)"
          >{{ str }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { getSuggesListAPI } from '@/api'
import { getStorage, setStorage } from '@/utils/storage'
export default {
  name: 'Search',
  data () {
    return {
      kw: '', // 搜索关键字
      timer: null, // 防抖的定时器
      suggesList: [], // 联想菜单列表
      history: JSON.parse(getStorage('his')) || []// 搜索历史
    }
  },
  methods: {
    // 输入框内容实时改变--触发方法
    inputFn () {
      // 重新计时
      clearTimeout(this.timer)
      // indexOf(' '),返回空格出现的索引位置
      if (this.kw.length === 0 || this.kw.indexOf(' ') >= 0) { // 无值或者包含空格
        this.suggesList = []
      } else { // 有值
        // 防抖：延时执行逻辑代码，事件再次触发时，清除上一个定时器
        this.timer = setTimeout(async () => {
          if (this.kw.length === 0) return
          const res = await getSuggesListAPI({
            q: this.kw
          })
          console.log('获取联想菜单列表：', res)
          this.suggesList = res.data.data.options
        }, 300)
      }
    },
    // 专门处理高亮
    lightFn (originStr, target) {
      // originStr：原来的字符串
      // target：关键字
      // 字符串.replace()   替换  replaceAll  全部替换
      // 返回值：替换后的完整字符串

      // 如果你要使用变量作为正则的匹配条件，不能使用语法糖简化写法
      // 创建一个正则对象
      const reg = new RegExp(target, 'ig')// i忽略大小写，g全局都匹配

      // 替换后的值不能是target
      return originStr.replace(reg, (target) => {
        return `<span style="color:red;">${target}</span>`
      })
    },
    // 输入框-搜索事件
    searchFn () {
      if (this.kw.length > 0) { // 当输入框有数值，回车才会执行跳转和数组添加
        // 搜索关键字--保存到数组中
        this.history.push(this.kw)

        // 因为watch是异步操作，下面的先执行
        setTimeout(() => {
          // params参数，如果写path，只能通过拼接方式进行传参
          this.$router.push({
            path: `/search_result/${this.kw}`
          })
        }, 0)
      }
    },
    // 联想菜单--点击事件
    suggestClickFn (str) {
      this.history.push(str)
      setTimeout(() => {
        this.$router.push({
          path: `/search_result/${str}`
        })
      }, 0)
    },
    // 历史记录--点击事件
    historyClickFn (str) {
      this.$router.push({
        path: `/search_result/${str}`
      })
    },
    // 清除历史记录--点击事件
    clearFn () {
      this.history = []
    }
  },
  // watch侦听器
  watch: {
    history: { // 历史记录数组改变
      deep: true,
      handler () {
        // 立刻覆盖式的保存到本地
        // JSON.stringify 转为字符串
        // JSON.parse转为原来的类型
        const theSet = new Set(this.history)// 对象
        // console.log(theSet)
        const arr = Array.from(theSet)// 转化为数组
        setStorage('his', JSON.stringify(arr))
      }
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }
  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}
/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
